.hide-description{
    display: none;
}
.show-description{
    text-align: start;
    display: block;
    
}
.content{
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    border-radius: 1rem;
    padding: 1rem;
    border: 0px solid grey;
    margin: 5rem;
}
.title-content{
    justify-content: space-between;
    display: flex;
}
.title-content p{
    font-weight: bold;
}
.title-content button{
    cursor: pointer;
    font-size: 1.5rem;
    color: orange;
    text-align: center;
    width: 2rem;
    height: 2rem;
    border: none;
    border-radius: 100%;
}
.title-content button:hover{
    transition: 0.2s;
    background-color: rgb(0, 0, 0,0.5);
}